import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			order_id:'',	//订单编号id
			product_id:'',	//商品id，只有当type为3时该属性有用
			type:'',		//商品的类型1:商品  3:兑换商品
 			time:'',
			providerList: [],	//支付列表
			currentLoadFlag: true,
			newsList: [], //全部宝贝、新品、活动数组集合
			tabIndex: 0, //当前下标
			scrollInto: '', //scroll跳转到这个Id
			tabBars: [
				{
					title: '全部'
				},
				{
					title: '待付款'
				},
				{
					title: '待发货'
				},
				{
					title: '待收货'
				},
				{
					title: '待评价'
				}
			]
		};
	},
	onLoad(e) {
		this.initInfo();
		this.tabIndex = e.index;
































	},
	onShow() {
		this.newsList[this.tabIndex].data = [];
		this.newsList[this.tabIndex].currentPage = 1;
		this.newsList[this.tabIndex].loadingFlag = false;
		this.newsList[this.tabIndex].nodata = false;
		this.newsList[this.tabIndex].refreshFlag = true;
		this.getOrderList(this.tabIndex);
	},
	methods: {
		//初始化newsList数据类型
		initInfo() {
			this.tabBars.forEach(tabBar => {
				this.newsList.push({
					data: [],
					refreshText: '',
					loadingStatus: 'more',
					refreshStatus: 'loading',
					currentPage: 1,
					pageSize: 6,
					loadingFlag: false,
					refreshFlag: true,
					nodata: false
				});
			});
		},

		// 加载更多，
		loadMore() {
			let that=this;
			if (that.currentLoadFlag) {
				that.currentLoadFlag = false;
				that.newsList[that.tabIndex].loadingStatus = 'loading';
				that.newsList[that.tabIndex].loadingFlag = true;
				setTimeout(function(){
					that.getOrderList(that.tabIndex);
				},400);
			}
			//保证每次只能请求一次
			setTimeout(() => {
				that.currentLoadFlag = true;
			}, 500);
		},
		//下拉刷新

		//导航栏点击跳转
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.tabIndex = index;
		},
		//左右滑动
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;
			//重置信息
			this.newsList[this.tabIndex].data = [];
			this.newsList[this.tabIndex].currentPage = 1;
			this.newsList[this.tabIndex].loadingFlag = false;
			this.newsList[this.tabIndex].nodata = false;
			this.newsList[this.tabIndex].refreshFlag = true;
			this.getOrderList(this.tabIndex);
		},
		//0.8s后隐藏加载提示的信息
		closeLoadingFlag() {
			let that = this;
			setTimeout(function() {
				that.newsList[that.tabIndex].loadingFlag = false;
			}, 800);
		},
		//获取用户所有订单
		getOrderList(index) {
			let that = this;
			that.basePost(
				that.U({ c: 'user_api', a: 'get_user_order_list' }),
				{
					//status状态的代表意思:'':查询所有;0:未付款;1:代发货;2:待收货;3:已收货;4:待评价;-1:退货申请中;-2：已退款;
					status: index == 0 ? '' : index == 1 ? 0 : index == 2 ? 1 : index == 3 ? 2 : 3,
					page: that.newsList[index].currentPage,
					limit: that.newsList[index].pageSize,
					search: ''
				},
				function(res) {
					that.newsList[that.tabIndex].refreshFlag = false;
					uni.stopPullDownRefresh();
					//判断该次级下是否有信息存在，以及查询出来的结果是否等于0条。如果是就显示没数据图片，否则不显示。
					that.newsList[index].nodata =(res.data.length == 0 && that.newsList[index].data== 0)?true:false;
					if (res.data.length == 0) { 
						that.newsList[index].loadingStatus = 'noMore';
						that.closeLoadingFlag();
						return;
					}
					that.newsList[index].data = [...that.newsList[index].data, ...res.data];
					that.newsList[index].currentPage++;
				},
				function(res) {
					console.log(res);
				}
			);
		},
		// tab
		tabsChange(index) {
			this.current = index;
		},
		//立即购买
		purchase(inde,index) {
			let that=this;
			that.order_id = that.newsList[inde].data[index].order_id;
			that.type = that.newsList[inde].data[index].type;
			that.product_id = that.newsList[inde].data[index].cartInfo[0].productInfo.id;








			
		},
		async requestPayment() {
			let that=this;
		    that.providerList[0].loading = true;
		    let orderInfo = await that.getOrderInfo_uniapp(that.providerList[0].id);
		    if (orderInfo.statusCode !== 200) {
		        uni.showModal({
		            content: "获得订单信息失败",
		            showCancel: false
		        })
		        return;
		    }
		    uni.requestPayment({
		        provider: that.providerList[0].id,
		        orderInfo: orderInfo.data,
		        success: (e) => {
		            uni.showToast({
		                title: "支付成功!"
		            })
					if(that.type == 3){
						that.updatePayStatus();						//兑换订单
					}else{
						that.updateProductPayStatus();		//商品订单 这一步是由后台回调处理的，本不应该出现在前端
					}
		        },
		        fail: (e) => {
		            uni.showModal({
		                content: "用户退出支付，支付失败",
		                showCancel: false
		            })
					if(that.type == 3){
						that.updatePayStatus();						//兑换订单
					}else{
						that.updateProductPayStatus();		//商品订单 这一步是由后台回调处理的，本不应该出现在前端
					}
					clearInterval(that.time);
		        },
		        complete: () => {
		            that.providerList[1].loading = false;
		        }
		    })
		},
		
		getOrderInfo_uniapp(e) { 
		    let appid = "";



		    let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=0.1' ;
		    return new Promise((res) => {
		        uni.request({
		            url: url,
		            success: (result) => {
		                res(result);
		            },
		            fail: (e) => {
		                res(e);
		            }
		        })
		    })
		},
		
		//定时查询是否已经支付
		watchPayStatus(order_id){
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'watch_pay_status',
					q: {
						order_id: order_id,
					}
				}),
				function(res) {
					if(res.data.paid==1){
						clearInterval(that.time);
						uni.redirectTo({
							url:"/pages/shop/successful_payment/successful_payment"
						})
					}
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		
		//茶叶商品的订单——支付成功之后修改订单状态
		updateProductPayStatus(){
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'update_product_pay_status',
					q: {
						order_id: that.order_id,
					}
				}),
				function(res) {
				},
				function(res) {
				},
				true
			);
		},
		
		//兑换商品的订单——支付成功之后修改订单状态
		updatePayStatus(){
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'exchange_update_pay_status',
					q: {
						order_id: that.order_id,
						product_id:that.product_id,
					}
				}),
				function(res) {
					console.log(res);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		
		//取消订单
		closeOrder(order_id, inde, index) {
			let that = this;
			uni.showModal({
				title: '提示',
				content: '确定删除这个订单？',
				success: function(res) {
					if (res.confirm) {
						that.basePost(
							that.U({ c: 'user_api', a: 'user_remove_order' }),
							{
								uni: order_id
							},
							function(res) {
								that.newsList[inde].data.splice(index, 1);
							},
							function(res) {
								console.log(res);
							}
						);
					} else {
						return;
					}
				}
			});
		},
		//确认收货
		confirmGood(order_id, inde, index) {
			let that = this;
			that.basePost(
				that.U({ c: 'user_api', a: 'user_take_order' }),
				{
					uni: order_id
				},
				function(res) {
					that.Tips({ title: '确认成功,立即前往评价~' });
					that.newsList[inde].data.splice(index, 1);
					that.tabIndex = 4;
				},
				function(res) {
					console.log(res);
				}
			);
		},
		//查看详情
		details(order_id) {
			uni.navigateTo({
				url: '/pages/user/order_details/order_details?order_id='+order_id
			});
		},
		//评价
		evaluation(inde, index) {
			let listUnique=[];
			let cartInfo=this.newsList[inde].data[index].cartInfo;
			for(var i=0;i<cartInfo.length;i++){
				let info={
					unique:cartInfo[i].unique,
					image:cartInfo[i].productInfo.image
				}
				listUnique.push(info);
			}
			uni.navigateTo({
				url: '/pages/user/my_evaluation/my_evaluation?listUnique='+JSON.stringify(listUnique)
			});
		},
		//查看物流
		logistics(order_id) {
			uni.navigateTo({
				url: '/pages/shop/shop_logistics/shop_logistics?order_id='+order_id
			});
		}
	}
};
